.facetwp-facet {
  margin-bottom: 2rem;
}

.facetwp-search-wrap {
  display: block;
  width: 85%;

  .facetwp-btn {
    line-height: 3rem;
    opacity: 1;
    top: 15px;
    right: 15px;

    &::before {
      background-image: url('../images/icon-search.svg');

      html[data-contrast="true"] & {
        background-image: url('../images/icon-search-white.svg');
      }
    }
  }
}

.facet-label {
  @extend h3;

  font-family: $font-sans;
  text-transform: uppercase;
}

.facetwp-checkbox.facetwp-checkbox {
  background-image: url('../images/unchecked.png');
  background-position: 0 0.4em;
  line-height: 1.25em;
  padding-bottom: 0.5em;
  margin-bottom: 0;

  &.checked {
    background-image: url('../images/checked.png');

    html[data-contrast="true"] & {
      background-image: url('../images/checked-white.png');
    }
  }
}

.facetwp-overlay.facetwp-overlay {
  background-color: rgba($color-green, 0.7);

  html[data-contrast="true"] & {
    background-color: rgba($color-black, 0.7);
  }
}

.facetwp-counts {
  padding: 1em 0;
  color: $color-gray-medium;
  font-size: 0.8em;
  width: 100%;
}

.resource {
  margin: 3em 0;
  font-weight: 300;

  .meta {
    .label {
      font-weight: 500;
    }
  }

  .content {
    margin-top: 1em;

    p {
      margin-top: 0;
    }
  }
}

.resource:first-of-type {
  margin-top: 0;
}

.pagination .facetwp-pager ul {
  padding-left: 0;

  li {
    margin: 0;

    &::before {
      content: "";
      width: 0;
    }
  }
}



